                :max-date="maxDate"
                @confirm="comfirmDateSelectedDb"
                @cancel="datePickerDb = false"
            />
        </van-popup>
    </div>
</template>

<script>
  import calendar from "../../components/calendar";
  export default {
    name: "water",
    components: {
      calendar
    },
    data () {
      return {
        marker: [
            {"date":"2020-12-01","className":"level2"},
            {"date":"2020-12-02","className":"level1"},
            {"date":"2020-12-03","className":"level2"},
            {"date":"2020-12-04","className":"level2"},
            {"date":"2020-12-05","className":"level2"},
            {"date":"2020-12-06","className":"level2"},
            {"date":"2020-12-07","className":"level2"},
            {"date":"2020-12-08","className":"level2"},
            {"date":"2020-12-09","className":"level1"},
            {"date":"2020-12-10","className":"level2"},
            {"date":"2020-12-11","className":"level3"},
            {"date":"2020-12-12","className":"level4"},
            {"date":"2020-12-13","className":"level4"},
            {"date":"2020-12-14","className":"level1"},
            {"date":"2020-12-15","className":"level1"},
            {"date":"2020-12-16","className":""},
            {"date":"2020-12-17","className":""},
            {"date":"2020-12-18","className":""},
            {"date":"2020-12-19","className":""},
            {"date":"2020-12-20","className":""},
            {"date":"2020-12-21","className":""},
            {"date":"2020-12-22","className":""},
            {"date":"2020-12-23","className":""},
            {"date":"2020-12-24","className":""},
            {"date":"2020-12-25","className":""},
            {"date":"2020-12-26","className":""},
            {"date":"2020-12-27","className":""},
            {"date":"2020-12-28","className":""},
            {"date":"2020-12-29","className":""},
            {"date":"2020-12-30","className":""},
            {"date":"2020-12-31","className":""}
        ],
        minDate: new Date(2010, 0, 1),
        maxDate: new Date(),
        datePicker: false,
        datePickerDb: false,
        currentDate: new Date(),
        currentDateDb: new Date(),
        options: {
          srtokeWidth: 2.4  // svg
        },
        selectTimeActive: ["active"],
        selectActive: [["active"],["active"],["active"],["active"],["active"],["active"]],
        lineHoursEcharts: {}, // 
        countDayEcharts: {}, // 2020
        pieContribution: {}, // 2020
        firstWaste: {}, // 2020
        exceedDay: {}, // 2020
        monthStatic: {}, // 
        stationValue: "", // 
        stationPicker: false, // 
        stationColumns: ["A","B"], // 
        factorValue: "", // 
        factorPicker: false, // 
        factorColumns: ["COD", 'pH', '', '', '', ''], // 
        factorValueTb: "",
        factorPickerTb: false, // 
        factorColumnsTb: ["COD", 'pH', '', '', '', ''], // 
        timeClassSelectedPicker: false,
        timeClass: "", // 
        timeClassPicker: false, // 
        timeClassColumns: ["","", ""], // 
      }
    },
    mounted() {
      // 
      this.initDashboard();
      // 
      this.drawLineHoursData();
      // 
      this.drawLineMonthStatic();
      // 
      this.drawLineMonthStaticTb();
    },
    methods: {
      // 
      goto(number){
      },
      comfirmDateSelected(value){
        this.datePicker = false
        this.$refs.calendar.ChoseMonth(value.format("yyyy-MM"))
      },
      comfirmDateSelectedDb(value){
        this.datePickerDb = false
      },
      // 
      drawLineHoursData(){
        this.lineHoursEcharts = this.$echarts.init(document.getElementById("echarts"))
        let option  = {
          color: "#E5CE10",
          xAxis: {
            type: 'category',
            axisTick: {
              alignWithLabel: true,
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: '#ddd', // 
                width: 1 // 
              },
            },
            axisLabel: {
              rotate: 40,
              color: "#666"
            },
            data: ['131','132','133','134','135','136']
          },
          tooltip: {
            trigger: 'axis'
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            top: '10%',
            containLabel: true
          },
          yAxis: {
            type: 'value',
            axisLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            splitLine:{
              show: true,
              lineStyle: {
                color: "#ddd"
              }
            },
            max: 300,
            interval: 60,
          },
          series: [{
            name: 'sdata',
            data: [67, 89, 123, 256, 200, 111],
            type: 'bar',
            barWidth: 7,
            itemStyle: {
              normal: {
                //
                color: function(params) {
                  return "#24C768";
                  // switch (that.dataItem.LEVEL[params.dataIndex]) {
                  //   case "":
                  //     return "#24C768"
                  //     break;
                  //   case "":
                  //     return "#E5CE10"
                  //     break;
                  //   case "":
                  //     return "#FF7E00"
                  //     break;
                  //   case "":
                  //     return "#FF0000"
                  //     break;
                  //   case "":
                  //     return "#990000"
                  //     break;
                  //   case "":
                  //     return "#7E0000"
                  //     break;
                  // }
                }
              }
            },
            backgroundStyle: {
              color: 'rgba(220, 220, 220, 0.8)'
            }
          }]
        }
        this.lineHoursEcharts.setOption(option)
      },
      // 
      drawLineMonthStatic(){
        this.monthStatic = this.$echarts.init(document.getElementById('echarts6'))
        let option = {
          // title: {
          //   text: '(g/m3)',
          //   textStyle: {
          //     color: '#666666',        // 
          //     fontStyle: 'normal',     // 
          //     fontWeight: 'normal',    // 
          //     fontFamily: 'Microsoft yahei',   // 
          //     fontSize: 14,     // 
          //     align: 'center'   // 
          //   },
          // },
          tooltip: {
            show: true
          },
          legend: [
            {
              x: "left",
              y: "top",
              data: ["2018"],
            },
            {
              x: "25%",
              y: "top",
              data: ["2019"],
            },
            {
              x: "50%",
              y: "top",
              data: ["2020"],
            }
          ],
          xAxis: {
            type: 'category',
            data: ["5", "6", "7", "8", "9", "10"],
            axisLabel: {
              show: true,
              type: 'category',
              boundaryGap: false,
              textStyle: {
                color: "#666666",
                fontSize: 12
              },
              lineStyle: {
                color: '#ddd',
              }
            },
            axisLine: {
              lineStyle: {
                color: '#ddd', // 
                width: 1 // 
              }
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false,
            },
          },
          yAxis: {
            interval: 50,
            min: 0,
            max: 150,
            type: 'value',
            axisTick: {
              show: false
            },
            axisLabel: {
              color: '#666666'
            },
            axisLine: {
              show: false
            },
          },
          grid: {
            top: "16%",
            left: "8%",
            bottom: "15%",
            right: "4%"
          },
          series: [{
            name: "2018",
            data: [32,54,121,68,89,111],
            type: 'line',
            smooth: true,
            symbolSize: 8,   //
            itemStyle: {
              normal: {
                color: '#1EBCBC', //
                lineStyle: {
                  color: '#1EBCBC' //
                }
              }
            }
          },{
            name: "2019",
            data: [45,77,111,67,112,21],
            type: 'line',
            smooth: true,
            symbolSize: 8,   //
            itemStyle: {
              normal: {
                color: '#FF7017', //
                lineStyle: {
                  color: '#FF7017' //
                }
              }
            }
          },{
            name: "2020",
            data: [34,56,87,55,120,111],
            type: 'line',
            smooth: true,
            symbolSize: 8,   //
            itemStyle: {
              normal: {
                color: '#1EBC52', //
                lineStyle: {
                  color: '#1EBC52' //
                }
              }
            }
          }]
        };
        this.monthStatic.setOption(option);
      },
      // 
      drawLineMonthStaticTb(){
        let monthStatic = this.$echarts.init(document.getElementById('echarts7'))
        let option = {
          tooltip: {
            show: true
          },
          title: {
            text: "",
            textStyle: {
              fontSize: 12,
              fontWeight: 100
            }
          },
          xAxis: {
            type: 'category',
            data: ["5", "6", "7", "8", "9", "10"],
            axisLabel: {
              show: true,
              type: 'category',
              boundaryGap: false,
              textStyle: {
                color: "#666666",
                fontSize: 12
              },
              lineStyle: {
                color: '#ddd',
              }
            },
            axisLine: {
              lineStyle: {
                color: '#ddd', // 
                width: 1 // 
              }
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false,
            },
          },
          yAxis: {
            interval: 50,
            min: 0,
            max: 100,
            type: 'value',
            axisTick: {
              show: false
            },
            axisLabel: {
              color: '#666666',
              formatter: function (value) {
                    return value + "%"
              }
            },
            axisLine: {
              show: false
            },
          },
          grid: {
            top: "16%",
            left: "12%",
            bottom: "15%",
            right: "4%"
          },
          series: [{
            name: "2019",
            data: [45,77,12,45,25,21],
            type: 'line',
            smooth: true,
            symbolSize: 8,   //
            itemStyle: {
              normal: {
                color: '#FF7017', //
                lineStyle: {
                  color: '#FF7017' //
                }
              }
            }
          }]
        };
        monthStatic.setOption(option);
      },
      // 
      selectPicker(number){
        if( number == 1 ) {
          this.stationPicker = true
        } else if( number == 2 ) {
          this.factorPicker = true
        } else if( number == 3 ) {
          this.datePicker = true
        } else if( number == 4 ) {
          this.factorPickerTb = true
        } else if( number == 5 ) {
          this.datePickerDb = true
        } else if( number == 6 ) {
          this.timeClassSelectedPicker = true
        }
      },
      // 
      onStationConfirm(value, index) {
        this.stationValue = value;
        this.stationPicker = false;
      },
      onTimeClassConfirm(value, index){
        this.timeClass = value;
        this.timeClassSelectedPicker = false;
      },
      // 
      onFactorConfirm(value, index){
        this.factorValue = value;
        this.factorPicker = false;
      },
      // ()
      onFactorConfirmTb(value, index){
        this.factorValueTb = value;
        this.factorPickerTb = false;
      },
      // 
      changeItem(key, index){
        this.$set(this.selectActive, key, [])
        this.$set(this.selectActive[key], index, "active")
      },
      selectTime(number){
        this.selectTimeActive         = []
        this.selectTimeActive[number] = "active"
      },
      // 
      initDashboard(){
        let echarts = this.$echarts.init(document.getElementById("dashboard"))
        let option = {
          tooltip: {
            formatter: '{b}{c}'
          },
          series: [{
            tooltip: {
              show: false
            },
            type: 'gauge',
            radius: '100%',
            center: ['50%', '60%'],
            startAngle: 180,
            endAngle: 0,
            detail: {
              show: false
            },
            axisLine: {
              lineStyle: {
                width: 4,
                color: [
                  [0.19, '#CCFFFF'],
                  [0.39, '#00CCFF'],
                  [0.59, '#00FF00'],
                  [0.79, '#FFFF00'],
                  [0.99, '#FF9B00'],
                  [1, '#FF0000'],
                ]
              }
            },
            axisLabel: {
              show: false
            },
            axisTick: {
              show: false,
            },
            splitLine: {
              show: false,
            },
          }, {
            type: 'gauge',
            radius: '100%',
            min: 0,
            max: 5,
            center: ['50%', '60%'],
            data: [{
              value: 4,
              name: ''
            }],
            splitNumber: 5,
            startAngle: 180,
            endAngle: 0,
            title: {
              offsetCenter: ['-50%', '20%'],
              fontSize: 12,
              color: "#fff"
            },
            detail: {
              formatter: function(value) {
                return ""
              },
              fontSize: 40,
              color: "#fff",
              offsetCenter: ['0%', '40%']
            },
            axisLine: {
              lineStyle: {
                width: 0,
                color: [
                  [0.19, '#CCFFFF'],
                  [0.39, '#00CCFF'],
                  [0.59, '#00FF00'],
                  [0.79, '#FFFF00'],
                  [0.99, '#FF9B00'],
                  [1, '#FF0000'],
                ]
              }
            },
            axisLabel: {
              color: '#fff',
              fontSize: 10,
              distance: 0,
              formatter: function(value) {
                if(value >= 5) {
                  return ""
                } else if (value >= 4) {
                  return ''
                } else if (value >= 3) {
                  return ''
                }
                else if (value >= 2) {
                  return ''
                }
                else if (value >= 1) {
                  return ''
                }
                else if (value >= 0) {
                  return ''
                }
              }
            },
            axisTick: {
              splitNumber: 10,
              show: true,
              lineStyle: {
                color: 'auto',
                width: 2
              },
              length: 10,
            },
            splitLine: {
              show: true,
              length: 14,
              lineStyle: {
                color: 'auto',
                width: 2
              }
            },
            itemStyle: {
              normal: {
                color: 'auto'
              }
            },
            pointer: {
              width: 5,
              length: '50%'
            }
          }]
        };
        echarts.setOption(option)
      },
      clickDay(value){
      },
      changeMonth(){},

    }
  }
</script>